.hero {
  position: relative;
  height: 100%;
  overflow: hidden;
  .container {
    height: 100%;
  }
  &__wrapper {
    @include flex-v-center;
    position: relative;
    height: 100%;
    @include m {
        display: block;
        margin-top: 140px;
    }
  }
  &__data {
    max-width: 523px;
    @include m {
        text-align: center;
    }
    .title {
      font-weight: bold;
      font-size: 48px;
      line-height: 64px;
      margin-bottom: 22px;
      @include a {
        font-size: 24px;
        line-height: 32px;
        margin-bottom: 13px;
      }
    }
    .info {
      font-size: 20px;
      line-height: 24px;
      margin-bottom: 33px;
      @include a {
        font-size: 12px;
        line-height: 16px;
        margin-bottom: 13px;
      }
    }
  }
  &__img {
    position: absolute;
    top: -700px;
    right: -500px;
    z-index: -1;
    @include m {
      top: -100px;
      right: -120px;
      img {
        width: 800px;
        height: auto;
      }
    }
    @include s {
        right: -100px;
    }
  }
}
