.nav {
  font-family: "Athiti", sans-serif;
  @include m {
    position: absolute;
    top: 0;
    left: 0;
    order: 2;
    @include flex-all-center;
    width: 100%;
    height: 100%;
    background: var(--color-secondary);
    padding: 0; // 30px
    max-height: 0;
    overflow: hidden;
    transition: all 0.3s ease-in-out;
    &.is-active {
      padding: 30px 0;
      height: 100vh;
      max-height: 100vh;
    }
  }

  &__list {
    @include flex-v-center;
    @include m {
      display: block;
      text-align: center;
    }
  }
  &__item {
    &:not(:last-of-type) {
      margin-right: 35px;
      @include m {
        margin-right: 0;
      }
    }
    @include m {
      margin-right: 0;
      margin-bottom: 22px;
    }
  }
  &__link {
    color: var(--color-nav);
    font-size: 18px;
    font-weight: 400;
    position: relative;
    &:hover {
      color: var(--color-primary);
    }
    &.is-active {
      color: var(--color-primary);
    }
  }
}
