html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: var(--color-black);
  height: 100%;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: var(--color-primary);
}

.wrapper {
  //overflow: hidden;
  display: flex;
  flex-direction: column;
  height: 100%;
}

header {
  flex: 0 0 auto;
}
footer {
  flex: 0 0 auto;
}
.main {
  flex: 1 0 auto;
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.title {
  color: var(--color-primary-alt);
  span {
    color: var(--color-primary);
  }
}

/*Button*/
.btn {
  display: inline-block;
  background: linear-gradient(180deg, #34e06f 0%, #07b464 100%);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25),
    inset 0px 2px 4px rgba(255, 255, 255, 0.25),
    inset 0px -2px 4px rgba(0, 0, 0, 0.25);
  border-radius: 12px;
  text-transform: uppercase;
  color: var(--color-white);
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  padding: 14px 67px;
  &:hover {
    box-shadow: 0 4px 13px -3px rgba(0, 0, 0, 0.37);
  }
}
