@mixin burger {
    display: none;
    position: relative;
    width: 40px;
    height: 28px;
    cursor: pointer;
    @include m {
      display: block;
    }
    &:before,
    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 2px;
      border-radius: 10px;
      background-color: var(--color-primary);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }
    &:after {
      top: auto;
      bottom: 0;
    }
    &__line {
      position: absolute;
      top: calc(50% - 1px);
      left: 0;
      width: 100%;
      height: 2px;
      border-radius: 10px;
      background-color: var(--color-primary);
      transition: transform 0.3s ease-in-out;
    }
    &.is-active {
      &:before {
        top: 50%;
        transform: rotate(45deg); //rotate(-45deg)
        transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
      }
      &:after {
        top: 50%;
        transform: rotate(-45deg); //rotate(45deg)
        transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
      }
      .burger__line {
        transform: scale(0);
        transition: transform 0.3s ease-in-out;
      }
    }
  }
  